html {
  line-height: 1.15;
}

body {
  margin: 0;
}

main {
  display: block;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

a {
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: "PT Sans", "Arial", sans-serif;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  outline-offset: -2px;
}

::-webkit-file-upload-button {
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

* {
  box-sizing: border-box;
}

.page {
  font-family: "PT Sans", "Arial", sans-serif;
}

.container {
  margin: 0 auto;
  height: 100%;
  width: 100%;
  max-width: 1200px;
}
@media (min-width: 320px) {
  .container {
    max-width: 767px;
  }
}
@media (min-width: 768px) {
  .container {
    max-width: 1200px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img {
  display: block;
  max-width: 100%;
  max-height: 100%;
}

.wrapper {
  box-shadow: 10px 0 19px -17px #000000, -10px 0 19px -17px #000000;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  max-width: 1200px;
  margin: 0 auto;
  background-color: #ffffff;
}

body,
html {
  height: 100%;
  background-color: #f2f2f2;
  scroll-behavior: smooth;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #000000;
  margin: 0;
}

a {
  text-decoration: none;
  cursor: pointer;
}

@font-face {
  font-family: "PT Sans";
  src: local("PT Sans"), url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  src: local("PT Sans"), url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-style: normal;
  font-weight: 700;
  font-display: swap;
}
.page-header {
  background-color: #ffffff;
}

.page-header__logo {
  position: absolute;
  left: calc(50% - 93px);
  width: 185px;
  height: 96px;
}

.nav-list {
  margin-block-start: 0;
  margin-block-end: 0;
  padding-inline-start: 0;
  width: 100%;
  display: none;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%) bottom/800px 10px no-repeat #81b3d2;
}
@media (min-width: 768px) {
  .nav-list {
    background-color: #ffffff;
    background-image: none;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }
}

.nav-list__link {
  text-decoration: none;
  color: #ffffff;
  transition: 0.4s;
}
@media (max-width: 767px) {
  .nav-list__link {
    width: 100vw;
    display: block;
  }
}
@media (min-width: 768px) {
  .nav-list__link {
    color: #000000;
  }
}
.nav-list__link:active {
  opacity: 0.3;
  transition: 0.4s;
}
.nav-list__link:hover {
  text-decoration: none;
  color: #81b3d2;
  transition: 0.4s;
}
@media (min-width: 768px) {
  .nav-list__link--current {
    color: #766357;
  }
}

.nav-list__item {
  display: block;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  text-transform: uppercase;
  color: #000000;
  padding: 15px 0 17px 0;
  width: 20%;
  border-bottom: 1px solid #4886aa;
  border-top: 1px solid #bcd3e0;
}
@media (max-width: 767px) {
  .nav-list__item {
    width: 100%;
    height: 56px;
  }
  .nav-list__item:hover {
    background-color: #669ec0;
    transition: background-color 0.6s;
  }
  .nav-list__item:hover .nav-list__link {
    color: #ffffff;
  }
  .nav-list__item:active {
    background-color: #5496bd;
    transition: background-color 0.6s;
  }
  .nav-list__item:active .nav-list__link {
    color: rgba(255, 255, 255, 0.3);
  }
}
.nav-list__item:nth-child(2) {
  margin-right: 10%;
}
.nav-list__item:nth-child(3) {
  margin-left: 10%;
}
@media (min-width: 768px) {
  .nav-list__item {
    padding: 18px 0 17px 0;
    border: none;
    font-size: 14px;
    line-height: 21px;
  }
}

@media (min-width: 320px) {
  .main-nav__burger {
    display: block;
    width: 62px;
    height: 56px;
    background: url("../img/header/icon-menu23x22.svg") center center no-repeat;
  }
}
@media (min-width: 768px) {
  .main-nav__burger {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
}
.main-nav__burger:hover {
  opacity: 0.6;
  transition: opacity 0.4s;
}
.main-nav__burger:active {
  opacity: 0.3;
  transition: opacity 0.4s;
}

@media (max-width: 767px) {
  .main-nav--nojs .nav-list {
    position: static;
    display: flex;
    flex-direction: column;
    border-bottom: 56px #ffffff solid;
  }
  .main-nav--nojs .main-nav__burger {
    display: none;
  }
  .main-nav--nojs .nav-list__item {
    margin: 0;
  }
  .main-nav--nojs .page-header__logo {
    margin-top: 224px;
  }
}

.main-nav {
  min-height: 56px;
  display: flex;
  flex-direction: row-reverse;
}
@media (max-width: 767px) {
  .main-nav {
    width: 100%;
  }
}
.main-nav--opened .nav-list__item {
  margin: 0;
}
.main-nav--opened .nav-list {
  position: absolute;
  display: flex;
  flex-direction: column;
}
.main-nav--opened .main-nav__burger {
  position: absolute;
  background: url("../img/nav/icon-close.svg") center/22px 22px no-repeat rgba(255, 255, 255, 0);
}

