.radio-elem {
  display: none;
}

.radio-elem:checked + .radio-style::before {
  content: "";
  width: 16px;
  height: 16px;
  background-color: #333333;
  position: absolute;
  border-radius: 50px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.radio-elem:active + .radio-style::before {
  content: "";
  width: 16px;
  height: 16px;
  background-color: rgba(51, 51, 51, 0.3);
  position: absolute;
  border-radius: 50px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.radio-style {
  position: absolute;
  background-color: #f2f2f2;
  width: 46px;
  height: 46px;
  border-radius: 50px;
  align-self: center;
}

.radio-text {
  margin-left: 63px;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: #000000;
  align-self: center;
}

.checkbox-elem:checked + .checkbox-style {
  background-image: url("../img/form/tick.svg");
  background-position: center center;
  background-repeat: no-repeat;
}

.checkbox-elem {
  display: none;
}

.checkbox-elem:active + .checkbox-style {
  background-image: url("../img/form/tick-gray.svg");
  background-position: center center;
  background-repeat: no-repeat;
}

.checkbox-style {
  position: absolute;
  background-color: #f2f2f2;
  width: 46px;
  height: 46px;
  border-radius: 4px;
  align-self: center;
}

.checkbox-text {
